import React, { Component, Fragment } from 'react';
import { Typography, CircularProgress } from '@material-ui/core';
import { withStyles } from '@material-ui/styles';
import { PieChart, Pie, ResponsiveContainer, Cell } from 'recharts';
import { get } from "axios";
import { connect } from "react-redux";

const styles = theme => ({
  progress: {
    margin: theme.spacing(2),
  },
  center: {
    justifyContent: "center",
    display: "flex"
  }
});


const renderCustomizedLabel = (name, percent) => `${name}: ${(percent * 100).toFixed(0)}%`


class ToneAnalysis extends Component {
  state = {
    loading: true
  }

  componentDidMount() {
    this.props.dispatch({
      type: "LOAD_TONE",
      payload: get("/api/tone/search")
    })
  }

  render() {
    const { classes, data, loading } = this.props;

    return (
      <Fragment>
        <Typography variant="h5">Tone analysis</Typography>
        {!loading ? (
          <ResponsiveContainer width="100%" height={400}>
            <PieChart height={400}>
              <Pie 
                stroke="none" 
                data={data} 
                dataKey="value" 
                label={(param) => renderCustomizedLabel(param.name, param.percent)}
              >
                {data.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
        ) : (
          <div className={classes.center}>
            <CircularProgress color="secondary" />
          </div>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  data: state.toneData,
  loading: state.toneLoading
})

export default connect(mapStateToProps)(withStyles(styles)(ToneAnalysis));