const SENTIMENT_COLORS = ['#FC4A1A', '#4ABDAC', '#F7B733'];
const TONE_COLORS = ['#FC4A1A', '#4ABDAC', '#F7B733', '#89DA59', '#80BD9E', '#AEBD38', '#456'];

const reducer = (state = { sentimentLoading: true, toneLoading: true }, action) => {
  switch (action.type) {
    case 'LOAD_SENTIMENT_PENDING':
      return { ...state, sentimentLoading: true, sentimentData: undefined };
    case 'LOAD_SENTIMENT_FULFILLED':
        const fromServer = action.payload.data;
        const data = Object.entries(fromServer).map(([ key, value ]) => ({
          name: key,
          value: value
        }))
          .filter(item => !["average", "total"].includes(item.name))
          .sort((a, b) => a.name.localeCompare(b.name))
          .map((pie, index) => ({ ...pie, color: SENTIMENT_COLORS[index] }));
      return { ...state, sentimentLoading: false, sentimentData: data };
    case 'LOAD_TONE_PENDING':
      return { ...state, toneLoading: true, toneData: undefined };
    case 'LOAD_TONE_FULFILLED':
        const toneFromServer = action.payload.data;
        const toneData = Object.entries(toneFromServer).map(([ key, value ]) => ({
          name: key,
          value: value
        }))
          .filter(item => !("total" === item.name || item.name.includes("_")))
          .sort((a, b) => a.name.localeCompare(b.name))
          .map((pie, index) => ({ ...pie, color: TONE_COLORS[index] }));
      return { ...state, toneLoading: false, toneData };
    default:
      return state;
  }
}
  
export default reducer;