import React, { Component, Fragment } from 'react';
import { connect } from "react-redux";
import { Typography, CircularProgress } from '@material-ui/core';
import { PieChart, Pie, ResponsiveContainer, Cell } from 'recharts';
import { withStyles } from '@material-ui/styles';
import { get } from "axios";

const styles = theme => ({
  progress: {
    margin: theme.spacing(2),
  },
  center: {
    justifyContent: "center",
    display: "flex"
  }
});

const LABELS = {
  'negatives': 'Negative', 
  'positives': 'Positive', 
  'neutrals': 'Neutral'
};
const RADIAN = Math.PI / 180;

const renderCustomizedLabel = ({
  cx, cy, midAngle, innerRadius, outerRadius, percent, index, name
}) => {
  const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
  const x = cx + radius * Math.cos(-midAngle * RADIAN);
  const y = cy + radius * Math.sin(-midAngle * RADIAN);

  return (
    <text x={x} y={y} fill="white" textAnchor="middle" dominantBaseline="middle">
      {`${LABELS[name]}: ${(percent * 100).toFixed(0)}%`}
    </text>
  );
};

class SentimentAnalysis extends Component {
  state = {
    loading: true
  }

  componentDidMount() {
    this.props.dispatch({
      type: "LOAD_SENTIMENT",
      payload: get("/api/sentiment/search")
    })
  }

  render() {
    const { classes, loading, data } = this.props;
    return (
      <Fragment>
        <Typography variant="h5">Sentiment analysis</Typography>
        {!loading ? (
          <ResponsiveContainer width="100%" height={400}>
            <PieChart height={400}>
              <Pie 
                stroke="none" 
                data={data} 
                dataKey="value" 
                labelLine={false} 
                label={renderCustomizedLabel}
              >
                {data.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.color} />
                ))}
              </Pie>
            </PieChart>
          </ResponsiveContainer>
        ) : (
          <div className={classes.center}>
            <CircularProgress color="secondary" />
          </div>
        )}

      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  data: state.sentimentData,
  loading: state.sentimentLoading
})

export default connect(mapStateToProps)(withStyles(styles)(SentimentAnalysis));